// ===== HERO FORM =====

.form {

    &__field {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 0 -15px 32px;
        text-align: center;
        position: relative;
        .form_order & {
            flex-flow: column; }

        @include sm {
            flex-flow: column;
            margin: 20px 0;
            padding: 0 15px; } }
    &__input-group {
        flex-grow: 1;
        position: relative;
        margin: 0 15px;
        position: relative;
        border: 3px solid $grey;
        background-color: $white;
        .form_order & {
            margin-bottom: 20px; }
        @include sm {
            margin-bottom: 16px; } }
    &__icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 12px;
        font-size: 0;
        .icon {
            font-size: 14px;
            color: $grey;
            @include icenter; } }
    &__input {
        display: block;
        width: 100%;
        background-color: transparent !important;
        font-size: 14px;
        font-weight: 400;
        font-style: italic;
        padding-left: 34px;
        font-family: $astra;
        height: 43px;
        &::placeholder {
            background-color: inherit;
            font-size: inherit;
            font-weight: inherit;
            line-height: inherit;
            color: inherit; }
        &:focus {
            border-bottom-color: rgba(#fff, 0.7);
            &::placeholder {
                font-size: 0; } }
        &.error {
            color: rgba(#f00, 0.7); }
        @include sm {
            border-bottom-width: 1px; } }
    label.error {
        top: 100%;
        left: 0;
        right: 0;
        position: absolute;
        padding: 4px 0 0 0;
        font-size: 14px;
        color: red;
        width: 100%;
        text-align: left; }
    &__textarea {
        width: 100%;
        padding: 4px;
        resize: vertical;
        height: 100px;
        box-sizing: border-box;
        font-size: 14px;
        font-weight: 400;
        font-style: italic;
        font-family: $astra;
        outline: none; }
    &__submit {
        display: inline-block;
        height: 45px;
        line-height: 45px;
        font-size: 16px;
        min-width: 306px;
        text-align: center;
        color: $black;
        line-height: 1.83;
        text-align: center;
        background-color: $green;
        text-transform: uppercase;
        border: 1px solid darken($green, 40%);
        transition: background-color $tr, color $tr;
        .form_order & {
            width: 100%; }
        &:disabled {
            cursor: default; }
        &.disabled {
            opacity: 0.5; }
        &:hover {
            background-color: darken($green, 20%);
            color: $white; }
        @include md {
            font-size: 16px;
            padding: 16px 50px; }
        @include sm {
            font-size: 14px;
            padding: 14px 0;
            width: 100%; } } }







